<template>
	<view class="personal p16">
		<view class="header">
			<image class="image" src="../../static/images/personal.png" mode="aspectFill"></image>
			<view class="uni-flex introduction">
				<view class="">
					<text class="block f18">{{userInfo.nickname}}</text>
					<text class="block f12 mt10">人生没有彩排，每天都在现场直播。</text>
				</view>
				<image class="portrait" :src="userInfo.headimgurl" mode="aspectFill"></image>
			</view>
		</view>
		<view class="list mt15">
			<navigator url="info">
				<view class="list_l">
					<view>
						<image class="per_ico" src="../../static/images/information.png" mode="aspectFill"></image>
						<text class="title">个人信息</text>
					</view>

					<text class="iconfont iconright"></text>
				</view>
			</navigator>
			<navigator url="collection">
				<view class="list_l">
					<view>
						<image class="per_ico collect_ico" src="../../static/images/collect.png" mode="aspectFill"></image>
						<text class="title">我的收藏</text>
					</view>
					<text class="iconfont iconright"></text>
				</view>
			</navigator>
			<navigator url="myorder">
				<view class="list_l">
					<view>
						<image class="per_ico order_ico" src="../../static/images/order.png" mode="aspectFill"></image>
						<text class="title">我的工单</text>
					</view>
					<text class="iconfont iconright"></text>
				</view>
			</navigator>
		</view>
	</view>
</template>

<script>
export default {
	components: {},
	data() {
		return {
			userInfo:{
				nickName:'555',
				headimgurl:'https://thirdwx.qlogo.cn/mmopen/vi_32/FRz8685UjwO6LFY9M7RJ2W5uEiart6daYUwMLibZAIficAjibtmfcgAqdicBof8OArPSD3wGaycFgeYre11HVqdJ1Lw/132'
			}
		};
	},
	/*S 监听页面滚动*/
	onPageScroll(e) {},
	/*E 监听页面滚动*/
	/*S 初始数据加载*/
	onLoad(options) {
		this.userInfo = uni.getStorageSync('userInfo')
		this.handler_info(options);
	},
	/*E 初始数据加载*/
	onShow() {},
	methods: {
		/* S  是否取到个人信息*/
		handler_info(options){			
			alert(this.userInfo.nickname)
			if(!!this.userInfo.nickname){
				
			}else{
				let access = uni.getStorageSync('access');
				alert(options.code);
				if (!access) {
					this.goOauth();
					this.redirectCode = options.code;
					if (this.redirectCode) {
						this.getAccessToken();
					} else {
						this.goOauth();
					}
				}	
			}
		},
		/* E  是否取到个人信息*/
		/* S 获取 code */
		goOauth() {
			const callbackURL = encodeURIComponent('http://seats.easy8in.top/login/test'); //测试
			const wechatId = 'wx731bbb761b6df538'; //测试
			const redirectURI = `https://open.weixin.qq.com/connect/oauth2/authorize?appid=${wechatId}&redirect_uri=${callbackURL}&response_type=code&scope=snsapi_userinfo&state=STATE`;
			window.location.href = redirectURI;
		},
		getCode(name) {
			var reg = new RegExp('(^|&)' + name + '=([^&]*)(&|$)');
			var r = window.location.search.substr(1).match(reg);
			if (r != null) return decodeURI(r[2]);
			return null;
		},
		/*  S 获取acce_token */
		getAccessToken() {
			var _this = this;
			this.$minApi
				.wanteng_wechat({
					code: _this.redirectCode
				})
				.then(res => {
					uni.setStorageSync('access', res);
					uni.setStorageSync('openid',res.openid)
					_this.openId = res.openid;
					_this.getInfoUser();
				});
		},
		// S 获取用户信息
		getInfoUser() {
			var _this = this;
			this.$minApi
				.wanteng_userinfo({
					openid: _this.openId
				})
				.then(res => {
					uni.setStorageSync('userInfo', res);
					_this.userInfo = res
				});
		},
		/* S 授权 */
	}
};
</script>

<style lang="scss">
.personal {
	background-color: #ffffff;
	min-height: calc(100vh - 72px);
	max-height: calc(100vh - 94px);
	.header {
		height: 212upx;
		position: relative;
		background: linear-gradient(311deg, #f05138 0%, #ff9751 100%);
		border-radius: 20upx;
		.introduction {
			position: absolute;
			top: 30upx;
			left: 48upx;
			bottom: 30upx;
			color: #ffffff;
			align-items: center;
			.block {
				width:384upx;
				display: block;
			}
		}
		.image {
			width: 100%;
			height: 100%;
		}
		.portrait {
			width: 120upx;
			height: 120upx;
			border-radius: 50%;
			margin-left: 84upx;
		}
	}
	.list_l {
		height: 128upx;
		display: flex;
		justify-content: space-between;
		align-items: center;
		border-bottom: 2upx solid #e6e6e6;
		padding: 0upx 10upx;
		.all_color {
			color: #ff623e;
		}
		.title {
			color: #323233;
			padding-left: 20upx;
			font-size: 32upx;
			vertical-align: middle;
		}
	}
	.per_ico {
		width: 26upx;
		height: 36upx;
		vertical-align: middle;
	}
	.collect_ico {
		width: 36upx;
		height: 36upx;
	}
	.order_ico {
		width: 34upx;
		height: 34upx;
	}
}
</style>
